<template>
  <div class="container">
    <page-title first-title="账户信息"></page-title>
    <suppliers-base-info :suppliersInfo="suppliers_info" :suppliersId="suppliers_id"></suppliers-base-info>
    <div class="search-wrapper">
      <el-form :inline="true" :model="searchParams" class="demo-form-inline">
        <el-form-item label="结算单编号：">
          <el-input v-model="searchParams.statement_no" style="width:200px"></el-input>
        </el-form-item>
        <el-form-item label="生成日期：">
          <el-date-picker v-model="addtime" type="date" placeholder="选择开始日期" style="width:150px">
          </el-date-picker>
          -
          <el-date-picker v-model="endtime" type="date" placeholder="选择截至日期" style="width:150px">
          </el-date-picker>

          <div class="radio-wrapper">
            <el-radio-group size="small" v-model="timeselect" @change="selecttime">
              <el-radio-button v-for="(item, index) in timelist" :key="index" :label="item.value">{{
                item.name
              }}</el-radio-button>
            </el-radio-group>
          </div>
        </el-form-item>

        <div class="cuttline"></div>

        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
          <el-button @click="clear">清空</el-button>

          <el-button type="primary" @click="exportList" v-if="!isSettle">导出账单详情</el-button>
          <template v-if="role === 'all' || role === 'operater'">
            <el-button @click="exportPay" v-if="isSettle">生成付款清单</el-button>
            <el-switch v-model="isSettle" style="padding: 0 20px"></el-switch>
            <span v-if="isSettle">切换导出账单</span>
            <span v-else>切换生成付款清单</span>
          </template>

          <!-- <template v-if="suppliers_id">
            <el-button type="primary" @click="exportList" v-if="!checkboxStatus">导出账单详情</el-button>
            <el-button type="primary" @click="navToMultiForm" v-else>汇总结算单</el-button>
          </template>
          <template v-else>
            <el-button type="primary" @click="exportList">导出账单详情</el-button>
          </template> -->
        </el-form-item>
        <!-- <el-form-item class="switch-multi" label="汇总结算单" v-if="suppliers_id">
          <el-switch v-model="checkboxStatus"></el-switch>
        </el-form-item> -->
      </el-form>
    </div>
    <div class="list-wrapper">
      <!-- <div class="button-wrapper">
        <el-radio-group v-model="searchParams.type" @change="change">
          <el-radio-button label="1">日汇总</el-radio-button>
          <el-radio-button label="2">月汇总</el-radio-button>
        </el-radio-group>
      </div> -->
      <el-table
        :data="list"
        style="width: 100%"
        @select="handleSelection"
        @select-all="selectAll"
        ref="statementList"
        v-loading="isLoading"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <template v-if="suppliers_id">
          <el-table-column
            type="selection"
            :selectable="checkboxFilter"
            v-if="checkboxStatus"
            key="single"
          ></el-table-column>
          <el-table-column type="selection" v-else key="multi" :selectable="filterSelect"></el-table-column>
        </template>
        <template v-else>
          <el-table-column type="selection"></el-table-column>
        </template>
        <el-table-column prop="statement_no" label="结算单编号"></el-table-column>
        <el-table-column prop="addtime" label="结算生成日期" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="add_cash" label="现金收入（元）"></el-table-column>
        <el-table-column
          prop="add_huanbi"
          label="积分收入（个）"
          class-name="dark"
          v-if="cooperateType == 1"
        ></el-table-column>
        <el-table-column prop="sub_cash" label="现金支出（元）"></el-table-column>
        <el-table-column
          prop="sub_huanbi"
          label="积分支出（个）"
          class-name="dark"
          v-if="cooperateType == 1"
        ></el-table-column>
        <el-table-column prop="cash" label="现金结算（元）"></el-table-column>
        <el-table-column
          prop="huanbi"
          label="积分结算（个）"
          class-name="dark"
          v-if="cooperateType == 1"
        ></el-table-column>
        <el-table-column prop="status" label="状态" :formatter="statusFormat"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="viewDetail(scope.row.suppliers_statement_id)" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="searchParams.size"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import { getSuppliersInfo, getSuppliersStatementList, exportList, statementPreview } from '../../api/suppliers'
import { mapState, mapMutations } from 'vuex'
import { saveAs } from 'file-saver'
import { TIMELIST } from './static'
import SuppliersBaseInfo from '../../components/suppliers/SuppliersBaseInfo'

export default {
  name: 'SuppliersStatementList',
  data() {
    return {
      checkboxStatus: false, // 列表属性，默认false为导出结算单，true为汇总结算单
      isSettle: false, //汇总or结算单 true为汇总单
      statementIds: [],
      total: 0,
      currentPage: 1,
      addtime: '',
      endtime: '',
      searchParams: {
        type: 1, //账单类型 1：日账单，2:月账单
        suppliers_id: '',
        size: 10,
        page: 1,
        addtime: '', //添加时间
        endtime: '',
        statement_no: '' //结算单号
      },
      list: [],
      suppliers_info: {
        suppliers_name: '',
        shop_name: '',
        manager_name: '',
        manager_tel: ''
      },
      selectObj: {}, //导出结算单
      statementStatus: {
        '1': '未结算',
        '2': '待结算',
        '3': '已结算'
      },
      timelist: TIMELIST,
      timeselect: '',
      isLoading: false //是否正在加载
    }
  },
  components: {
    SuppliersBaseInfo
  },
  created() {
    this.getInfo()
    this.getStateList()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      role: state => state.settle.role,
      cooperateType: state => state.suppliers.cooperateType
    })
  },
  watch: {
    // 监听checkboxStatus变化，发生变化则取消选择状态，清空selectObj
    checkboxStatus() {
      this.$refs.statementList.clearSelection()
      this.selectObj = {}
    }
  },
  methods: {
    ...mapMutations({
      saveStatementPreview: 'saveStatementPreview',
      saveStatementIdList: 'saveStatementIdList'
    }),
    getInfo() {
      getSuppliersInfo(this.suppliers_id).then(res => {
        this.suppliers_info = res
      })
    },
    getStateList() {
      this.isLoading = true
      this.searchParams.addtime = parseInt(this.addtime / 1000)
      this.searchParams.endtime = parseInt(this.endtime / 1000)
      this.searchParams.suppliers_id = this.suppliers_id
      getSuppliersStatementList(this.searchParams)
        .then(
          res => {
            this.list = res.list
            this.total = res.paged.total
          },
          error => {
            console.log(error)
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    },
    viewDetail(id) {
      this.$router.push({ name: 'SuppliersStatementInfo', params: { id: id } })
    },
    handleCurrentChange(currentPage) {
      this.searchParams.page = currentPage
      this.getStateList()
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
    },
    selecttime(val) {
      let now = new Date().getTime()
      let now0 = new Date(new Date().toLocaleDateString()).getTime() //当天0点时间戳
      let now1 = new Date(new Date().toLocaleDateString()).getTime() - 24 * 3600 * 1e3 //昨天0点时间戳
      let now7 = new Date(new Date().toLocaleDateString()).getTime() - 7 * 24 * 3600 * 1e3 //7天前0点时间戳
      let now30 = new Date(new Date().toLocaleDateString()).getTime() - 30 * 24 * 3600 * 1e3 //30天前0点时间戳
      let start
      let end = now
      if (!val) {
        start = now0
      } else if (val === 1) {
        start = now1
        end = now0 - 1
      } else if (val === 7) {
        start = now7
      } else if (val === 30) {
        start = now30
      }
      this.addtime = start
      this.endtime = end
      this.getStateList()
    },
    clear() {
      this.searchParams.statement_no = ''
      this.addtime = ''
      this.endtime = ''
      this.timeselect = ''
      this.getStateList()
    },
    search() {
      this.searchParams.page = 1
      this.getStateList()
    },
    handleSelection(selection, row) {
      if (this.selectObj[row.suppliers_statement_id]) {
        this.changeSelectState([row], false)
      } else {
        this.changeSelectState([row], true)
      }
    },
    selectAll(row) {
      if (row.length) {
        this.changeSelectState(row, true)
      } else {
        this.changeSelectState(this.list, false)
      }
    },
    changeSelectState(row, flag) {
      const obj = { ...this.selectObj }
      if (flag) {
        row.forEach(item => {
          obj[item.suppliers_statement_id] = item
        })
      } else {
        row.forEach(item => {
          delete obj[item.suppliers_statement_id]
        })
      }
      this.selectObj = { ...obj }
    },
    exportList() {
      let list = [...Object.keys(this.selectObj)]
      if (!list.length) {
        return
      }
      exportList({
        suppliers_id: this.suppliers_id,
        suppliers_statement_id: list
      }).then(
        res => {
          const date = this.utils.formatDate('YYYY-MM-DD', new Date().getTime())
          saveAs(new Blob([res.data]), res.filename || this.suppliers_info.suppliers_name + date + '结算单.xls')
        },
        error => {
          console.log(error)
        }
      )
    },
    exportPay() {
      let list = [...Object.keys(this.selectObj)]
      list = list.map(x => Number(x))
      this.saveStatementIdList(list)
      if (!list.length) {
        return
      }
      statementPreview(this.suppliers_id, list).then(
        res => {
          res.all_cash = res.all_cash > 0 ? res.all_cash : 0
          res.all_huanbi = res.all_huanbi > 0 ? res.all_huanbi : 0
          this.saveStatementPreview(res)
          this.$router.push({ name: 'SettlementAdd' })
        },
        error => {
          console.log(error)
        }
      )
    },

    checkboxFilter(row) {
      var isSelect = row.status != 1 ? true : false
      return isSelect
    },

    // 跳转到创建汇总结算单页
    navToMultiForm() {
      var list = [...Object.keys(this.selectObj)]
      if (!list.length) {
        return
      }

      this.$router.push({
        name: 'MultiSuppliersStatementForm',
        params: { statements: this.selectObj, type: 'create' }
      })
    },
    statusFormat(row, column) {
      let date = row[column.property]
      return this.statementStatus[date]
    },
    filterSelect(row) {
      if (this.isSettle) {
        let isSelect = row.status > 1 ? false : true
        return isSelect
      } else {
        return true
      }
    }
    // 月汇总&日汇总
    // change(val) {
    //   this.currentPage = 1
    //   this.getStateList()
    // }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/style/custom.css';
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}

.container {
  font-size: 15px;
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }
  .search-wrapper {
    padding: 10px;
    margin: 10px 0;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 0;
    }
    .switch-multi {
      margin-left: 20px;
    }
    .cuttline {
      height: 10px;
    }
    .radio-wrapper {
      display: inline-block;
      padding: 0 4px;
      border-radius: 4px;
      background-color: #eee;
      margin-left: 10px;
    }
  }
  .button-wrapper {
    margin-bottom: 10px;
  }
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
